@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');
*{
    padding: 0; margin: 0; box-sizing: border-box;font-family: 'Mulish', sans-serif;
}
.navbar{
    background-color: #fff!important;
}
.navbar-brand{
    font-size: 1.9rem!important;
    color: #565387!important;

}
.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}
.menu_active{
    font-weight: bold; border-bottom: 1px solid#565387;
}
.navbar a:hover{
    color: #3498db!important;
}
@media (max-width:991px){
    .navbar-brand{
        font-size: 1.5rem!important;
    }
    .menu_active{
        font-weight: bold;
        border-bottom: none;
    }
}
.brand-name{
    color: blue; 
    text-shadow: 2px 2px 2px orange;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}
#header h2{
    color: #484848;
    font-size: 24px;
}




.header-img{
    text-align: left;
    
}
.img-fluid animated{
    animation: up-down2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
    0%{
        transform:translateY(10px) ;
    }
    100%{
        transform:translateY(-10px) ;
    }
}
@keyframes up-down{
    0%{
        transform:translateY(10px) ;
    }
    100%{
        transform:translateY(-10px) ;
    }
}
.App {
    text-align: center;

  }
  
  .App-logo {
    height: 40vmin;
    align-items: center;
    pointer-events: none;
  }
  .jan{
    animation: beat .25s infinite alternate;
    transform-origin: center;
}
@keyframes beat {
   to{transform: scale(1.1); }
}